let navItem = document.querySelectorAll(".vertical-nav__active");
let c = 8;
let heightSection;
if (window.innerWidth > 1280) {
    heightSection = 1000;
} else if (window.innerWidth < 1280) {
    heightSection = 768;
}
function removeActive() {
    for (i = 0; i < navItem.length; i++) {
        if (navItem[i].classList.contains("active")) {
            navItem[i].classList.remove("active");
        }
    }
}
for (i = 0; i < navItem.length; i++) {
    navItem[i].addEventListener('click', function(e) {
        e.stopPropagation();
        removeActive();
    })
}
window.addEventListener('scroll', function() {
    if (window.scrollY >= 0 && window.scrollY < heightSection) {
        for (let i = 0; i < navItem.length; i += c){
            navItem[i].classList.add("active");
            navItem[i + 1].classList.remove("active");
        }
    } else if (window.scrollY >= heightSection && window.scrollY < 2 * heightSection) {
        for (let i = 1; i < navItem.length; i += c){
            navItem[i].classList.add("active");
            navItem[i - 1].classList.remove("active");
            navItem[i + 1].classList.remove("active");
        }
    } else if (window.scrollY >= 2 * heightSection && window.scrollY < 3 * heightSection) {
        for (let i = 2; i < navItem.length; i += c){
            navItem[i].classList.add("active");
            navItem[i - 1].classList.remove("active");
            navItem[i + 1].classList.remove("active");
        }
    } else if (window.scrollY >= 3 * heightSection && window.scrollY < 4 * heightSection) {
        for (let i = 3; i < navItem.length; i += c){
            navItem[i].classList.add("active");
            navItem[i - 1].classList.remove("active");
            navItem[i + 1].classList.remove("active");
        }
    } else if (window.scrollY >= 4 * heightSection && window.scrollY < 5 * heightSection) {
        for (let i = 4; i < navItem.length; i += c){
            navItem[i].classList.add("active");
            navItem[i - 1].classList.remove("active");
            navItem[i + 1].classList.remove("active");
        }
    } else if (window.scrollY >= 5 * heightSection && window.scrollY < 6 * heightSection) {
        for (let i = 5; i < navItem.length; i += c){
            navItem[i].classList.add("active");
            navItem[i - 1].classList.remove("active");
            navItem[i + 1].classList.remove("active");
        }
    } else if (window.scrollY >= 6 * heightSection && window.scrollY < 7 * heightSection) {
        for (let i = 6; i < navItem.length; i += c){
            navItem[i].classList.add("active");
            navItem[i - 1].classList.remove("active");
            navItem[i + 1].classList.remove("active");
        }
    } else if (window.scrollY >= 7 * heightSection) {
        for (let i = 7; i < navItem.length; i += c){
            navItem[i].classList.add("active");
            navItem[i - 1].classList.remove("active");
        }
    }
})
//slider-burgers
$(document).ready(function() {
    if ($(".slick-slider").length > 0 ) {
        $(".slick-slider").slick({
            arrows: true,
            dots: false,
            infinite: true,
            // autoplay: 1500,
            // speed: 1000,
            slidesToShow: 1,
            adaptiveHeight: true,
            prevArrow: "<img src='img/arrow-left.png'>",
            nextArrow: "<img src='img/arrow-right.png'>",
        });
    }
    //header-nav click
    $("#nav-list").on("click",".header__nav-item-link", function (event) {
        removeActive();
        event.preventDefault();
        $('body,html').animate({scrollTop: $($(this).attr('href')).offset().top}, 500);
        });
});
//menu-click
let menuItem = document.querySelectorAll('.menu__item');
if (window.innerWidth > 768) {
    for (let i = 0; i < menuItem.length; i++) {
        menuItem[i].addEventListener('click', function(e) {
            e.preventDefault();
            menuItem[i].classList.toggle('active');
        })
    }
}
if (window.innerWidth <= 768) {
    for (let i = 0; i < menuItem.length; i++) {
        menuItem[i].addEventListener('click', function(e) {
            e.preventDefault();
            for (let j = 0; j < menuItem.length; j++) {
                if (menuItem[j].classList.contains('active') && menuItem[j] !== menuItem[i]) {
                    menuItem[j].classList.toggle('active');
                }
            }
            menuItem[i].classList.toggle('active');
        })
    }
}

